
.topic-title{margin:.35rem;padding:.12rem;font-size:18px;color:#2c3e50;line-height:1.5;background-color:#f0f0f0;border-radius:.12rem
}
.topic-info{padding:0 .35rem;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;color:#34495e;font-size:12px
}
.topic-info>img{margin-right:.35rem;width:1rem;height:1rem;border-radius:50%
}
.topic-info>.author-info>*,.topic-info>.topic-reviews>*{display:block;padding:.05rem 0
}
.topic-info>.author-info{-ms-flex:1;flex:1
}
.topic-info>.topic-reviews>.tag{padding:.1rem .12rem;font-size:10px;font-weight:400;border-radius:.08rem;background-color:#e7e7e7;text-align:center;vertical-align:middle
}
.topic-info>.topic-reviews>.top{background-color:#E74C3C
}
.topic-info>.topic-reviews>.good{background-color:#E67E22
}
.topic-info>.topic-reviews>.ask{background-color:#3498DB
}
.topic-info>.topic-reviews>.share{background-color:#1ABC9C
}
.topic-info>.topic-reviews>.job{background-color:#9B59B6
}
.markdown-body{padding:0 .3rem
}
.markdown-body li{list-style:initial
}
.topic-reply{padding:.18rem;font-size:.3rem;font-weight:normal;border-left:.1rem solid #00a24a
}
.topic-reply>em{font-style:normal;color:#00a24a
}
.reply-list li{padding:.3rem .2rem
}
.reply-list li>.reply-head{display:-ms-flexbox;display:flex;line-height:1
}
.reply-list li>.reply-head img{width:.8rem;height:.8rem;margin-right:.2rem;display:block;vertical-align:baseline;border-radius:50%
}
.reply-list li>.reply-head>.reply-info{-ms-flex:1;flex:1;padding-top:.2rem;font-size:0;text-align:left;color:#626262
}
.reply-list li>.reply-head>.reply-info>.name{padding-right:.1rem;font-size:.3rem
}
.reply-list li>.reply-head>.reply-info>.date{font-size:.33rem
}
.reply-list li>.reply-head>.like{display:inline-block;padding-top:.1rem;width:1.9rem;font-size:.3rem;text-align:right
}
.reply-list li>.reply-head>.like .iconfont{font-size:.5rem
}
.reply-list li .reply-content{padding-top:.2rem
}


