
.vue-drawer {
  display: block;
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.vue-drawer>.main {
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: transform ease-in-out 0.38s, visibility 0.38s;
}
.vue-drawer>.main>.mask {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: none;
  z-index: 9;
  opacity: 0;
  transition: opacity ease-in-out 0.38s, visibility ease-in-out 0.38s;
  background-color: rgba(0, 0, 0, 0.3);
}
.vue-drawer>.main>.active {
  display: block;
  opacity: 1;
}
.vue-drawer>.drawer {
  max-width: 600px;
  background-color: #fff;
  position: absolute;
  top: 0;
  z-index: 10;
  height: 100%;
  overflow: hidden;
  pointer-events: none;
  visibility: hidden;
  transition: transform ease-in-out 0.38s, visibility 0.38s;
  will-change: none;
}
.vue-drawer>.drawer-left {
  left: 0;
  transform: translateX(-102%);
}
.vue-drawer>.drawer-right {
  right: 0;
  transform: translateX(102%);
}
.vue-drawer>.active {
  pointer-events: inherit;
  visibility: visible;
  transform: translateX(0%);
}



